import React from "react";
import { connect } from "react-redux";
import Hoc from "../hoc/hoc";
import { Input, Image, Comment } from "semantic-ui-react"
import WebSocketInstance from "../websocket";
import { fetchMessages } from "../store/actions/message";

/**
 * @return {string}
 */
function NumScroll(num) {
  if (num > 16) {
    return 'scroll'
  } else {return 'null'}
}

class Chat extends React.Component {
  state = {
    message: "",
  };

  initialiseChat() {
    this.props.fetchMessages(this.props.chatID)
    if (this.props.active) {
      WebSocketInstance.connect(this.props.chatID);
    }
  }

  constructor(props) {
    super(props);
    this.initialiseChat();
  }

  messageChangeHandler = event => {
    this.setState({ message: event.target.value });
  };

  sendMessageHandler = e => {
    e.preventDefault();
    const messageObject = {
      content: this.state.message
    };
    WebSocketInstance.newChatMessage(messageObject);
    this.setState({ message: "" });
  };

  renderMessages = messages => {
    return messages.map((message) => (
      <Comment key={message.id}>
        <Comment.Content>
          <Comment.Author as='a' style={{color: '#' + message.author.username_color}}>
            <Image className="message-img" src='https://static-cdn.jtvnw.net/badges/v1/5527c58c-fb7d-422d-b71b-f309dcb85cc1/3' />
            <Image className="message-img" src='https://static-cdn.jtvnw.net/jtv_user_pictures/panel-148316617-image-3036e147-20a6-490c-ae31-9cfa6cdd73ad' />
            <Image className="message-img" src='data:image/png;base64,' />
            {message.author.username}
          </Comment.Author>

          <Comment.Metadata>
            <div>Today at 5:42PM</div>
          </Comment.Metadata>

          <Comment.Text>{message.text}</Comment.Text>
        </Comment.Content>
      </Comment>
    ));
  };

  scrollToBottom = () => {
    this.messagesEnd.scrollIntoView({ behavior: "smooth" });
  };

  componentDidMount() {
    this.scrollToBottom();
  }

  componentDidUpdate() {
    this.scrollToBottom();
  }

  componentWillUnmount() {
    if (WebSocketInstance.socketRef) {
      WebSocketInstance.disconnect();
    }
  }

  render() {
    const { messages, active } = this.props;

    return (
      <Hoc>
        <div className="messages">
          <Comment.Group style={{height: '90%', overflowY: NumScroll(17)}}>

          {messages && this.renderMessages(messages)}

          <div
            ref={el => {
              this.messagesEnd = el;
            }}
          />

          </Comment.Group>
        </div>

        <div className="message-input">
          {active ?
            <form autoComplete="off" onSubmit={this.sendMessageHandler}>
              <div className="wrap">
                <Input
                  onChange={this.messageChangeHandler}
                  value={this.state.message}
                  required
                  id="chat-message-input"
                  type="text"
                  placeholder="Write your message..."
                  fluid={true}
                />
              </div>
            </form> : 'This stream is offline'
          }
        </div>
      </Hoc>
    );
  }
}

const mapStateToProps = state => {
  return {
    messages: state.message.messages
  };
};

const mapDispatchToProps = dispatch => {
  return {
    fetchMessages: (chatID) => dispatch(fetchMessages(chatID))
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(Chat);